/* Header */
.header {
	padding: 40px 0;
	min-height: 695px;

	display: flex;
	align-items: center;

	background-color: var(--header-bg);
	background-image: url('./../../img/headerbg.jpg');
	background-size:cover;
	background-repeat: no-repeat;
	background-position: center 70%;

	color: var(--header-text);
	text-align: center;
}

.header__wrapper {
	padding: 0 15px;
	max-width: 1170px;
	margin: 0 auto;
}

.header__title {
	max-width: 600px;
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color: var(--accent);
}

.header__text {
	margin-bottom: 40px;
	font-size: 18px;
	line-height: 1.333;
}

.header__text p + p {
	margin-top: 0.5em;
}