.image-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0 -10px;
    align-items:baseline;
    
  }
  
 
  .image-gallery__item {
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
    vertical-align: top;
    width: calc(33.33% - 10px);
    text-align: center;
    cursor: pointer;
  }
  .image-gallery__image{
    max-height: 500px;
    border-radius: 3%;
  }
  
  .image-gallery__description {
    margin-top: 5px;
    font-weight: 400;
    color: var(--text-color);
    text-align: center;
    font-size: 20px;
  }
  
  
  .image-gallery__modal {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 100;
  }
  
  .image-gallery__modal-image {
    max-width: 100%;
    max-height: 100%;
    margin: auto;
    cursor: pointer;
    border-radius: 3%;
  }
  .image-gallery__modal-description {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 1.2rem;
    color: white;
    text-align: center;
    width: 100%;
    max-width: 300px;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.8);
  }
    