.video-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 900px;
    margin: 0 auto;
  }
  
  .video-block {
    margin: 10px;
    text-align: center;
    width: calc(33.33% - 20px);
    max-width: 300px;
    font-size: 18px;
}

.video-block img {
      border-radius: 5%;
    cursor: pointer;
    max-width: 100%;
    min-height: 210px;
  }
  
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
  }
  
  .modal-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  
  .modal-content {
    position: relative;
    width: 80%;
    max-width: 800px;
    height: 0;
    padding-bottom: 56.25%;
    overflow: hidden;
    border-radius: 5px;
  }
  
  .modal-content iframe {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-height: 120vh;
    width: 100%;
  }
  